.form-control {
  // border: 1px solid $border-color;
  // height: calc(2.25rem + 2px);
  // font-weight: normal;
  // font-size: $input-font-size;
  // padding: 0.625rem 0.6875rem;
  // background-color: $input-bg;
  // border-radius: 2px;
  // color: #ffffff;

  // border: 1px solid #2c2e33; 
  height: calc(2.25rem + 2px);
  font-weight: normal;
  font-size: 0.875rem;
  padding: 0.625rem 0.6875rem;
  color: #ffffff;
  // width: 1626px;
  width: 670px;
  max-height: 963px;
  border-radius: 20px;
  // backdrop-filter: blur(10px);
  // background-color: #d8d8d824;
  width: 380px;

  background-color: #323232;
  box-shadow: 2px 3px 8px 0px #000000bf;
  text-align: center;



  &:focus {
    // border: 1px solid rgba(71, 164, 71, 0.5);
    background-color: $input-bg;
  }

  &.form-control-lg {
    padding: 0.94rem .75rem;
    height: calc(2.875rem + 2px);
  }

  &.form-control-sm {
    padding: 0.5rem .75rem;
    height: calc(1.8125rem + 2px);
  }

  form-control &::placeholder {
    color: $text-muted;
    font-size: $input-placeholder-font-size;
  }
}

.navbar {
  .form-control {
    width: 75%;
    border-radius: 6px;

    &:focus {
      border: none;
    }
  }
}

select {
  &.form-control {
    padding: .4375rem .75rem;
    border: 0;
    outline: 1px solid #322f2f;
    color: #ffffff;
    // width: 450px;
    width: 358px;
    margin-left: 7px;

    border: 1px solid #656565;

    // color: $input-placeholder-color;
    &:focus {
      outline: 1px solid $border-color;
    }

    @each $color,
    $value in $theme-colors {
      &.border-#{$color} {
        outline: 1px solid $value;

        &:focus {
          outline: 1px solid $value;
        }
      }
    }
  }
}

input.form-control {
  font-weight: 500;
}

.form-group {
  label {
    font-size: $default-font-size;
    line-height: 1;
    vertical-align: top;

    font-weight: 700;
  }

  &.has-danger {
    .form-control {
      border-color: theme-color(danger);
    }
  }

  .file-upload-default {
    visibility: hidden;
    position: absolute;
  }

  .file-upload-info {
    background: transparent;
  }
}

.form-check {
  min-height: 18px;
  padding-left: 1.25rem;
}

div {
  &.tagsinput {
    background: transparent;
  }

  .configuration {
    display: flex;
    justify-content: center;
  }

  .configuration.vms {
    display: flex;
    justify-content: center;
  }
}

.custom-file {
  .visibility-hidden {
    visibility: hidden;
  }

  .custom-file-label {
    background: $input-bg;
    ;
    border: 1px solid $border-color;
    height: calc(2.25rem + 2px);
    font-weight: normal;
    font-size: 0.875rem;
    padding: 0.625rem 0.6875rem;
    border-radius: 2px;

    &:after {
      background-color: theme-color(primary);
      height: auto;
      @extend .btn-primary;
    }
  }
}

.form-check.service_type.setting {
  height: 2rem;
  margin-left: -4px;
}

.form-check.breath-mode {
  margin-top: 2px;

  // right: 240px;
  //   top: -1px;
}

.form-check .form-check-label input[type=checkbox]+.input-helper:before {
  content: "";
  width: 24px;
  height: 24px;
  border-radius: 2px;
  border: solid #0090e7;
  border-width: 2px;
  transition: all;
  transition-duration: 0s;
  transition-duration: 250ms;
  position: absolute;
  top: 2px;
  left: 0;

}

.form-check .form-check-label input[type=checkbox]+.input-helper:after {
  transition: all;
  transition-duration: 0s;
  transition-duration: 250ms;
  font-family: Material Design Icons;
  opacity: 0;
  filter: alpha(opacity=0);
  transform: scale(0);
  content: "";
  font-size: 0.875rem;
  font-weight: bold;
  color: #ffffff;
  top: 5px;
  left: -2px;
  font-size: x-large;
}

label.form-check-label.service_type.setting {
  line-height: 2.2;
  margin-right: 0.5rem;
  color: #e5e1e1;
  // margin-left: 5px;
