/* Tables */
@keyframes blink {
  50% {
    border-color: rgba(90, 94, 86, 0.5);
  }
}

.table {
  margin-bottom: 0;
  color: $text-muted;
  display: grid;
  table-layout: fixed;

  thead {
    th {
      border-top: 0;
      border-bottom-width: 0px;
      font-family: $type1;
      font-weight: $font-weight-medium;

      i {
        margin-left: 0.325rem;
      }

      text-align: center;
    }
  }

  tbody+tbody {
    border-top: none;
  }

  th,
  td {
    vertical-align: middle;
    font-size: $default-font-size;
    line-height: 1;
    padding: 0.76em;

    img {
      width: 30px;
      height: 30px;
      border-radius: 0%;
    }

    .badge {
      margin-bottom: 0;
    }
  }

  &.table-borderless {
    border: none;

    tr,
    td,
    th {
      border: none;
    }

    text-align: center;
  }
}

.table-bordered {
  border: 0;
}

.table thead th {
  border-bottom-width: 0px;
  font-family: "Rubik", sans-serif;
  // font-weight: 500;
  font-family: 'KorailRoundGothicBold';
}

.hidetext {
  -webkit-text-security: disc;
  /* Default */
}

.VMS.table.table-striped.table-bordered.table {
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: center;
  width: 78%;
  top: 4%;
  left: 10%;
  font-weight: 500;
}

table.VMS.table.table-striped.table-bordered {
  max-height: 406px;
  overflow-y: auto;
  overflow-x: hidden;
}

.VMS2.table.table-striped.table-bordered.table {
  display: flex;
  flex-direction: column;
  // margin: 1.8rem 1.8rem 0 1.6rem;
  margin: 1rem 1.8rem 0 1.6rem;

}

.LEFTBREATH.table.table-striped.table-bordered.table {
  display: flex;
  flex-direction: column;
  // margin: auto;
  margin-left: 5px;
  white-space: pre-line;
}

table.table.report-detail-severity {
  display: table-cell;
}

.table-responsive.report-detail {
  display: block;
  width: 100%;
  overflow-x: hidden;
}

th.report-detail-few-severity {
  padding: 0.9375rem 0.375rem;
  text-align: center;
  font-size: 0.8rem;
}

td.report-detail-few-severity {
  padding: 0.9375rem 0.375rem;
  text-align: center;
}

.detail-report.few {
  font-size: 0.75rem;
}

table.table.table-bordered.breathSensorValue {
  display: block;
  background-color: #000000b8;
  font-weight: 500;
}

td.registered-vmsId {
  max-width: 170px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

td.registered-vmsPW {
  max-width: 172px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -webkit-text-security: disc;
}

table.table.table-hover.breathSensorList {
  display: table;
}

.table-responsive.breathSensorList {
  height: 350px;
  overflow: auto;
}

i.mdi.mdi-heart-outline.breathState {
  display: flex;
  justify-content: center;
  color: red;
  animation: blinkBreathSensor 1s;
  animation-iteration-count: infinite;
}

span.breathSensorTextOnEvent {
  display: flex;
  justify-content: center;
  color: red;
  font-weight: bolder;
}

span.breathSensorTextOnDefault {
  display: flex;
  justify-content: center;
}


label.badge.badge-success {
  display: flex;
  justify-content: center;
}

td.registered-check {
  width: 3%;
}

td.registered-vmsIP {
  width: 40%;
}

td.registered-vmsPort {
  width: 15%;
}

td.registered-vmsId {
  width: 20%;
}

td.registered-vmsPW {
  width: 25%;
}

table.VMS.table.table-striped.table-bordered::-webkit-scrollbar {
  width: 13px;
}

table.VMS.table.table-striped.table-bordered::-webkit-scrollbar-thumb {
  background-color: #2f3542;
  border-radius: 10px;
  background-clip: padding-box;
  border: 2px solid transparent;
}

table.VMS.table.table-striped.table-bordered::-webkit-scrollbar-track {
  background-color: grey;
  border-radius: 10px;
  box-shadow: inset 0px 0px 5px white;
}

.table-striped tbody tr:nth-of-type(odd) {
  background-color: #0000008b;
  font-weight: 700;
}