/* Buttons */

.btn {
  font-size: $btn-font-size;
  line-height: 1;
  font-weight: normal;

  i {
    margin-right: .3rem;
  }

  &.btn-rounded {
    @include border-radius(50px);
  }

  &.btn-fw {
    width: 2.8rem;
    max-height: 42px;
    padding: 0.35rem 0.5rem;
  }

  &.btn-config {
    margin-top: 22px;
    height: 40px;
  }

  &.btn-sm {
    font-size: $btn-font-size-sm;
  }

  &.btn-lg {
    font-size: $btn-font-size-lg;
  }

  &.btn-xs {
    padding: $btn-padding-y-xs $btn-padding-x-xs;
    font-size: $btn-font-size-xs;
  }

  /* Buttons with only icons */
  &.btn-icon {
    width: 42px;
    height: 42px;
    padding: 0;
  }

  /* Buttons with icon and text */
  &.btn-icon-text {
    .btn-icon-prepend {
      margin-right: .5rem;
    }

    .btn-icon-append {
      margin-left: .5rem;
    }
  }

  &.btn-social-icon {
    width: 50px;
    height: 50px;
    padding: 0;
  }

  &.get-started-btn {
    font-size: 0.75rem;
    border: 1px solid rgba(#ffffff, 0.3);
    font-weight: $font-weight-medium;
    padding: 0.9375rem 1.0625rem;

    @media (max-width: 576px) {
      font-size: 0.6rem;
      padding: 0.4375rem 0.6625rem;
    }
  }
}



.btn-group {
  .btn {
    +.btn {
      border-left: 0;
    }
  }
}

.btn-toolbar {
  .btn-group {
    +.btn-group {
      @extend .ml-2;
    }
  }
}

/*social buttons*/
@each $color,
$value in $social-colors {
  .btn-#{$color} {
    @include social-button(social-color($color));
  }

  .btn-outline-#{$color} {
    @include social-outline-button(social-color($color));
  }
}

/* inverse buttons */
@each $color,
$value in $theme-colors {
  .btn-inverse-#{$color} {
    @include button-inverse-variant($value);
  }

  .btn-#{$color} {
    &:not(.btn-light):not(.btn-secondary) {
      color: $black;
      background: linear-gradient(133deg, #424242 -10%, #c2c2c2 30%, #C2C2C3 60%, #424242);
      border: 1.5px solid #bbbaba;
      box-shadow: 1px 1px 3px 1px black;
      font-weight: 700;
      text-shadow: 1px 1px #eae7e7;

      &:hover,
      &:focus,
      // &:active {
        {
        // color: $white;
      }

      &:active{
        outline: 0;
        outline-offset: 0;
        background: linear-gradient(133deg, #000000 -10%, #494848 30%, #787878 60%, #101010);
        text-shadow: 1px 1px #252525;
        border: 1.5px solid #0b0b0b;
        box-shadow: 0px 0px 2px 2.2px #4f4f4f;
        color: #b1b1b1;
        font-weight: bolder;
      }

      &:focus,
      &:active {
        // background: $value;
        // border-color: $value;
      }
    }

  }
}

