@font-face {
  font-family: 'KorailRoundGothicBold';
  src: local('KorailRoundGothicBold'), url('../font/Korail_Round_Gothic_Bold.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}

.navbar {
  position: absolute;
  background: transparent;
  z-index: 100;
  width: 100%;
  height: 3.85rem;
  font-family: 'KorailRoundGothicBold';

}

.navbar_wrapper {
  /* display: flex;
  justify-content: space-between;
  margin-left: 1rem;
  width: 100%; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 1rem;
  width: 100%;
  height: 6.5vh;
}

.observer_title {
  margin-left: 0;
  margin-top: 1rem;
  margin-bottom: 0;
  padding-left: 0;
}

.observer_title h2 {
  text-shadow: 2px 2px #ffffff;
    color: #000000;
    -webkit-text-stroke: 1px #7c7c7c;
}

.navbar_menu {
  position: absolute;
  top: 93.6vh;
  /* left: 10.2%;
    width: 15.5%; */
  left: 3%;
  width: 22.8%;
  height: 6vh;
  display: flex;
  align-items: center;
  background-color: #000000b8;
  border-radius: 10px;
  margin-left: 88.6rem;
  border: 1px solid #a7a7a7;
  box-shadow: 2px 2px 10px 3px black, inset 2px 3px 14px -4px #e0dede;
  margin-top: -1px;
}

.breathSensorList {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  /* width: 3rem; */
  color: #000000d5;
  background-color: #fff;
  border-radius: 50%;
  padding: 0 0.6rem;
  cursor: pointer;
  box-shadow: 2px 2px 7px 2px black, inset 0.5px 0.5px 3px 3px #0000005c;
  width: 48px;
  height: 48px;
}

.eventCount {
  position: relative;
  top: -0.9vh;
  left: -1.1vw;
  color: #dddddd;
  font-size: smaller;
}

.eventCount2 {
  position: relative;
  top: -0.8vh;
  left: -1.3vw;
  color: #dddddd;
  font-size: smaller;
}

.eventCount3 {
  position: relative;
  top: -0.8vh;
  left: -1.4vw;
  color: #dddddd;
  font-size: xx-small;
}

.eventBellImage {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 2.5vw;
  color: #000000d5;
  margin: 0;
  border-radius: 50%;
  /* padding: 0 0.6rem; */
  cursor: pointer;
  box-shadow: 2px 2px 7px 2px black, inset 0.5px 0.5px 3px 3px #0000005c;

  /* display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 3rem;
    color: #000000d5;
    background-color: rgba(255, 0, 0, 0.451);
    border-radius: 50%;
    padding: 0 0.6rem;
    cursor: pointer;
    box-shadow: 0px 0px 5px 4px #720b0b, inset 0.5px 0.5px 3px 3px #0000005c; */
}

.ack_status_img {
  position: relative;
  width: 2.5vw;
  margin-left: 0.8rem;
}

.navbar_items {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 0;
  /* margin-left: -102px; */
  margin-left: -8px;

}

.parkingService,
.inquiry_data,
.report,
.setting,
.crowd_density {
  color: #000;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  padding: 0 0.6rem;
  font-size: 1.75rem;
  cursor: pointer;
  box-shadow: 2px 2px 7px 2px black, inset 0.5px 0.5px 3px 3px #0000005c;
  width: 48px;
  height: 48px;
}

.view_dashboard {
  position: absolute;
  top: 93.8vh;
  cursor: pointer;
}

.login_dropDown {
  /* display: flex;
  width: 8rem; */
  display: flex;
  width: 7.5rem;
}

.loginId {
  display: flex;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: bold;
}

.profile {
  display: flex;
  align-items: flex-end;
  font-size: 1.1rem;
  margin-left: 7px;
}

.login_dropDown_toggle {
  display: flex;
  cursor: pointer;
  transition: transform 300ms ease-in-out;
}

.login_dropDown_toggle:hover {
  transform: scale(1.01);
  color: #e0e0e0;
}

.login_dropDown_toggle:after {
  display: none;
}