@keyframes blink {
  50% {
    border-color: rgba(90, 94, 86, 0.5);
  }
}

@keyframes blinkBreathSensor {
  50% {
    color: #BDC1D2;
  }
}

@keyframes blink_breathSensor {
  50% {
    border-color: #272F42;
  }
}

@keyframes bounce_frames {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(0, -50px, 0);
  }
}

@keyframes blink_effect {
  50% {
    opacity: 0;
  }
}

@keyframes scaleout {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  100% {
    transform: scale(1.2);
    -webkit-transform: scale(1.2);
    opacity: 0;
  }
}

@keyframes text-animation {
  0% {
    width: 0;
  }

  50% {
    width: 100%;
  }

  100% {
    width: 0;
  }
}


